import React from "react";
import { Paper, Divider, Typography, Grid } from "@material-ui/core";
import { makeStyles , MuiThemeProvider } from "@material-ui/core/styles";
import { storiesOf } from "@storybook/react";
import themes from "../../Theme/themes";
import { BrandType } from "../../types/brand";
import ScrollLoader from "../ScrollLoader";

const useStyles = makeStyles((theme) => ({
    root: {
        background: theme.palette.background.default,
        boxShadow: theme.shadows[8],
        padding: "30px",
    },
    darkBg: {
        background: "rgba(0, 0, 0, 0.6)",
        boxShadow: theme.shadows[8],
        padding: "30px",
    },
}));

type LoadingsProps = {
    brand: BrandType,
    mode: string,
}

const Loadings = ({ brand, mode }: LoadingsProps) => {
    const classes = useStyles(themes[brand]);

    return (
        <MuiThemeProvider theme={themes[brand]}>
            <Paper className={classes[mode]} id="botoes">
                <Typography variant="h3">Scroll Loader</Typography>
                <br />
                <Divider />
                <br />
                <Grid container direction="row" justify="flex-start" spacing={2}>
                    <Grid item xs={12}>
                        <ScrollLoader/>
                    </Grid>
                </Grid>
            </Paper>
        </MuiThemeProvider>

    );

};

const argsControls = {
    args: {
        brand: "CVC",
        mode: "root",
    },
    argTypes: {
        brand: {
            type: "select",
            options: ["BlackAndWhite", "BlackFriday", "CVC", "CVCCorp", "CVCYellow", "Sub"]
        },
        mode: {
            type: "select",
            options: ["root", "darkBg"]
        },
    }
}

storiesOf("Components/Loadings/ScrollLoader", module)
    .add("Default", (args) => <Loadings {...args}/>, argsControls)
